import React, { useState } from "react";
import DatePicker from "react-datepicker";
import moment from "moment";
import "./styles.css";
import "react-datepicker/dist/react-datepicker.css";

function Form(props) {
  const [inputField, setInputField] = useState({
    fromTime: "",
    toTime: "",
    furnace: "",
    furnaceLabel: "",
    customer: "",
    material: "",
    partName: "",
    partNumber: "",
    process: "",
    batchNo: "",
    batchQty: "",
    soakingStart: "",
    soakingEnd: "",
    totalSoakingTime: "",
    subTitle: "",
    interval: ""
  });

  const options = [
    { value: "", label: "Select Furnace" },
    { value: "CH1,CH15,CH3,CH19,CH2,CH16", label: "SQF1" },
    { value: "CH4,CH18,CH6,CH20,CH5,CH17", label: "SQF2" },
    { value: "CH7,CH24", label: "TF1" },
    { value: "CH8,CH25", label: "TF2" },
    { value: "CH9,CH28", label: "TF3" },
    { value: "CH10,CH22,CH12,CH21,CH11,CH23", label: "GCF" },
    { value: "CH13,CH27", label: "THF1" },
    { value: "CH14,CH26", label: "THF2" }
  ];

  const setSubTitle = () => {
    const fromTimeToDisplay = inputField.fromTime
      ? moment(inputField.fromTime).format("DD/MM/YYYY")
      : "";
    const toTimeToDisplay = inputField.toTime
      ? moment(inputField.toTime).format("DD/MM/YYYY")
      : "";
    const furnaceToDisplay = furnaceObjToDisplay
      ? furnaceObjToDisplay.label
      : "";
    inputField.furnaceLabel = furnaceToDisplay;
    return (
      "From: " +
      fromTimeToDisplay +
      " | To: " +
      toTimeToDisplay +
      " | Furnace: " +
      furnaceToDisplay +
      " | Customer: " +
      inputField.customer +
      " | Material: " +
      inputField.material +
      " | Part Name: " +
      inputField.partName +
      " | Part Number: " +
      inputField.partNumber +
      " | Process: " +
      inputField.process +
      " | Batch No: " +
      inputField.batchNo +
      " | Batch Qty: " +
      inputField.batchQty +
      " | Soaking Start: " +
      inputField.soakingStart +
      " | Soaking End: " +
      inputField.soakingEnd +
      " | Total Soaking Time: " +
      inputField.totalSoakingTime
    );
  };

  const furnaceObjToDisplay = options.find((obj) => {
    return obj.value === inputField.furnace;
  });

  const inputsHandler = (e) => {
    setInputField({ ...inputField, [e.target.name]: e.target.value });
  };

  const submitButton = (e) => {
    const subTitle = setSubTitle();
    props.callbackSubmit(inputField, subTitle);
  };

  const exportButton = (e) => {
    props.callbackExport(e);
  };

  const resetButton = (e) => {
    setInputField({
      fromTime: "",
      toTime: "",
      furnace: "",
      furnaceLabel: "",
      customer: "",
      material: "",
      partName: "",
      partNumber: "",
      process: "",
      batchNo: "",
      batchQty: "",
      soakingStart: "",
      soakingEnd: "",
      totalSoakingTime: "",
      subTitle: "",
      interval: ""
    });
  };

  const displayButton = (e) => {
    props.callbackDisplayReadings((displayValues) => !displayValues);
  };

  const handleChangeFromTime = (data) => {
    setInputField({ ...inputField, fromTime: data });
  };
  const handleChangeToTime = (data) => {
    setInputField({ ...inputField, toTime: data });
  };

  return (
    <div>
      <label htmlFor="customer">From: </label>
      <DatePicker
        name="fromTime"
        selected={inputField.fromTime}
        onChange={handleChangeFromTime}
        dateFormat="yyyy-MM-dd HH:mm:ss"
        timeInputLabel="Time:"
        timeFormat="HH:mm"
        startDate={inputField.fromTime}
        endDate={inputField.toTime}
        showTimeSelect
      />
      <br />
      <label htmlFor="customer">To: </label>
      <DatePicker
        name="toTime"
        selected={inputField.toTime}
        onChange={handleChangeToTime}
        dateFormat="yyyy-MM-dd HH:mm:ss"
        timeInputLabel="Time:"
        timeFormat="HH:mm:ss"
        startDate={inputField.fromTime}
        endDate={inputField.toTime}
        minDate={inputField.fromTime}
        showTimeSelect
      />
      <br />
      <label htmlFor="customer">Furnace: </label>
      <select
        type="text"
        name="furnace"
        onChange={inputsHandler}
        value={inputField.furnace}
      >
        {options.map((option) => (
          <option
            key={option.value}
            value={option.value}
            defaultValue={option[0]}
          >
            {option.label}
          </option>
        ))}
      </select>
      <br />
      <label htmlFor="customer">Customer: </label>
      <input
        type="text"
        name="customer"
        onChange={inputsHandler}
        value={inputField.customer}
      />
      <br />
      <label htmlFor="material">Material: </label>
      <input
        type="text"
        name="material"
        onChange={inputsHandler}
        value={inputField.material}
      />
      <br />
      <label htmlFor="partName">Part Name: </label>
      <input
        type="text"
        name="partName"
        onChange={inputsHandler}
        value={inputField.partName}
      />
      <br />
      <label htmlFor="partNumber">Part Number: </label>
      <input
        type="text"
        name="partNumber"
        onChange={inputsHandler}
        value={inputField.partNumber}
      />
      <br />
      <label for="process">Process: </label>
      <input
        type="text"
        name="process"
        onChange={inputsHandler}
        value={inputField.process}
      />
      <br />
      <label htmlFor="batchNo">Batch No: </label>
      <input
        type="text"
        name="batchNo"
        onChange={inputsHandler}
        value={inputField.batchNo}
      />
      <br />
      <label htmlFor="batchQty">Batch Qty: </label>
      <input
        type="text"
        name="batchQty"
        onChange={inputsHandler}
        value={inputField.batchQty}
      />
      <br />
      <label for="soakingStart">Soaking Start: </label>
      <input
        type="text"
        name="soakingStart"
        onChange={inputsHandler}
        value={inputField.soakingStart}
      />
      <br />
      <label htmlFor="soakingEnd">Soaking End: </label>
      <input
        type="text"
        name="soakingEnd"
        onChange={inputsHandler}
        value={inputField.soakingEnd}
      />
      <br />
      <label htmlFor="totalSoakingTime">Total Soaking Time: </label>
      <input
        type="text"
        name="totalSoakingTime"
        onChange={inputsHandler}
        value={inputField.totalSoakingTime}
      />
      <br />
      <label htmlFor="interval">Interval: </label>
      <input
        type="text"
        name="interval"
        onChange={inputsHandler}
        value={inputField.interval}
      />
      <br />
      <button onClick={submitButton}>Draw</button>
      <button onClick={resetButton}>Reset</button>
      <button onClick={exportButton}>Export</button>
      <button onClick={displayButton}>Readings</button>
    </div>
  );
}

export default Form;
