import { useState } from "react";
import { Chart2 } from "./Chart";
import "./styles.css";
import moment from "moment";
import html2canvas from "html2canvas";
import pdfConverter from "jspdf";
import { ChartInfo } from "./ChartInfo";
import Form from "./Form";

export default function App() {
  const [chartData, setChartData] = useState({});
  const [displayReadings, setDisplayReadings] = useState(false);

  const [subTitle, setSubTitle] = useState("");

  const setChartFormData = (chartData, subTitle) => {
    //setChartData(chartData);
    setSubTitle(subTitle);
    //https://mocki.io/v1/846e861f-8566-4006-bb21-59cac21a1fff
    fetchPrices(
      "http://localhost:8082/data",
      chartData.fromTime,
      chartData.toTime,
      chartData.furnace,
      chartData.furnaceLabel,
      chartData.interval
    );
  };

  const getTimePortion = (item) => {
    let time = item.timestamp;
    return time.substring(11, 16);
  };

  const setDisplayReadingsCallback = () => {
    setDisplayReadings((displayReadings) => !displayReadings);
  };

  const exportChart = (e) => {
    //const but = e.target;
    //but.style.display = "none";
    let input = window.document.getElementsByClassName("App ChartDiv")[0];

    html2canvas(input).then((canvas) => {
      console.log(canvas.width + "  " + canvas.height);
      const img = canvas.toDataURL("image/jpg");
      const pdf = new pdfConverter(
        "l",
        "pt",
        [canvas.width, canvas.height],
        true
      );
      pdf.addImage(img, "JPEG", 1, 1, canvas.width, canvas.height);
      pdf.save("chart.pdf");
      //but.style.display = "block";
    });
  };

  // const [chartData, setChartData] = useState(null);
  const fetchPrices = async (
    url,
    fromTime,
    toTime,
    furnace,
    furnaceLabel,
    interval
  ) => {
    url =
      url +
      "?fromTime=" +
      moment(fromTime).format("YYYY-MM-DD HH:mm:ss") +
      "&toTime=" +
      moment(toTime).format("YYYY-MM-DD HH:mm:ss") +
      "&furnace=" +
      furnace +
      "&interval=" +
      interval;
    console.log(url);
    const res = await fetch(url);
    const data = await res.json();

    let datasetfiltered = [];

    if (
      furnaceLabel === "SQF1" ||
      furnaceLabel === "SQF2" ||
      furnaceLabel === "GCF"
    ) {
      datasetfiltered = [
        {
          label: "Actual Process Temp",
          data: data.data.map((item) => item.actualMv),
          borderColor: "#ff6384",
          backgroundColor: "#ff6384",
          yAxisID: "y"
        },
        {
          label: "Set Process Temp",
          data: data.data.map((item) => item.setMv),
          borderColor: "#ff6384",
          backgroundColor: "#ff6384",
          yAxisID: "y"
        },
        {
          label: "Actual Oil Temp",
          data: data.data.map((item) => item.actualOt),
          borderColor: "#36a2eb",
          backgroundColor: "#36a2eb",
          yAxisID: "y"
        },
        {
          label: "Set Oil Temp",
          data: data.data.map((item) => item.setOt),
          borderColor: "#36a2eb",
          backgroundColor: "#36a2eb",
          yAxisID: "y"
        },
        {
          label: "Actual % CP",
          data: data.data.map((item) => item.actualCp),
          borderColor: "#cc65fe",
          backgroundColor: "#cc65fe",
          yAxisID: "y1"
        },
        {
          label: "Set % CP",
          data: data.data.map((item) => item.setCp),
          borderColor: "#cc65fe",
          backgroundColor: "#cc65fe",
          yAxisID: "y1"
        }
      ];
    } else {
      datasetfiltered = [
        {
          label: "Actual Process Temp",
          data: data.data.map((item) => item.actualMv),
          borderColor: "#ff6384",
          backgroundColor: "#ff6384",
          yAxisID: "y"
        },
        {
          label: "Set Process Temp",
          data: data.data.map((item) => item.setMv),
          borderColor: "#ff6384",
          backgroundColor: "#ff6384",
          yAxisID: "y"
        }
      ];
    }

    setChartData({
      labels: data.data.map((item) => getTimePortion(item)),
      datasets: datasetfiltered
    });
  };

  const resetForm = (obj) => {
    setInputField({});
  };

  return (
    <div className="App">
      <div className="App FormDiv">
        <Form
          callbackSubmit={setChartFormData}
          callbackExport={exportChart}
          callbackDisplayReadings={setDisplayReadingsCallback}
        />
      </div>

      <div className="App ChartDiv">
        <ChartInfo subtitle={subTitle} />
        <Chart2
          chartData={chartData}
          displayValues={displayReadings}
          subtitle={subTitle ? subTitle : ""}
        />
      </div>
    </div>
  );
}
