import { Line, Chart } from "react-chartjs-2";
import zoomPlugin from "chartjs-plugin-zoom";
import ChartDataLabels from "chartjs-plugin-datalabels";

Chart.register(zoomPlugin);
Chart.register(ChartDataLabels);

export const Chart2 = ({ chartData, subtitle, displayValues }) => {
  return (
    // style={{ height: "600px", width: "1900px" }}
    <div>
      <Line
        data={chartData}
        options={{
          responsive: true,
          interaction: {
            mode: "index",
            intersect: false
          },
          stacked: false,
          plugins: {
            title: {
              display: true,
              text: ["", ""]
            },
            legend: {
              position: "bottom"
            },
            zoom: {
              zoom: {
                min: 1,
                max: 1000,
                wheel: {
                  enabled: true // SET SCROOL ZOOM TO TRUE
                },
                drag: {
                  enabled: false // SET SCROOL ZOOM TO TRUE
                },
                mode: "x",
                speed: 100
              },
              pan: {
                enabled: false,
                mode: "xy",
                speed: 100
              }
            },
            datalabels: {
              display: displayValues,
              color: "black",
              align: "end",
              padding: {
                right: 2
              },
              labels: {
                padding: { top: 10 },
                title: {
                  font: {
                    //weight: "bold"
                  }
                },
                value: {
                  color: "green"
                }
              }
            }
          },
          scales: {
            y: {
              type: "linear",
              display: true,
              position: "left",
              ticks: {
                callback: function (value, index, ticks) {
                  return value + " Â°C";
                }
              }
            },
            y1: {
              type: "linear",
              display: true,
              position: "right",
              grid: {
                drawOnChartArea: false
              },
              ticks: {
                callback: function (value, index, ticks) {
                  return value + " CP";
                }
              }
            }
          }
        }}
      />
    </div>
  );
};
